import React, { Component } from 'react'
import { motion } from 'framer-motion'
import Item from './Item'

export class List extends Component {
    state = {
        items : [
            { id: 1, title: "first" },
            { id: 2, title: "secound" },
            { id: 3, title: "third" },
            { id: 4, title: "fourth" },
            { id: 5, title: "fith" }
        ],
        last : null,
        selectedItem : {title:null, index:null, code:null}
    }
    handleUp = (index) => {
        if(index === 0){ }
        else {
            const clone = this.state.items;

            const first = clone[index]
            const secound = clone[index-1]

            clone[index-1] = first
            clone[index] = secound
            this.setState({items: clone})
        }
    }
    handleDown = (index) => {
        const last = this.state.items.length
        console.log(last, index+1)
        if(index + 1 === last){ }
        else {
            const clone = this.state.items;

            const first = clone[index]
            const secound = clone[index+1]

            clone[index+1] = first
            clone[index] = secound
            this.setState({items: clone})
        }
    }
    handleDelete = (index) => {
        console.log("delete", index)

        const clone = this.state.items
        clone.splice(index, 1)

        this.setState({last: this.state.last - 1})
        this.setState({items: clone})
    }
    handleAdd = (title) => {
        const {items} = this.state;
        const clone = items;
        const id = clone.length+1;
        
        const value = {id, title};
        clone.push(value);
        console.log(clone)

        this.setState({last: this.state.last + 1})
        this.setState({items: clone})
    }
    handleEdit = (selected, index) => {
        const inp = document.getElementById("Edit")
        const selectedItem = this.state.selectedItem
        selectedItem.index = index
        selectedItem.code = inp
        inp.setAttribute("value", selected)
    }
    editeState = (title, index) => {
        const inp = document.getElementById("Edit")
        console.log(title, index)
        const clone = this.state.items;
        clone[index].title = inp.value
        console.log(`clone[index].title`, clone[index].title)
        console.log(clone)
        this.setState({items: clone})
        inp.value=null
    }
    componentDidMount() {
        this.setState({last : this.state.items.length})
    }
    
    render() {
        const {items} = this.state;

        return (
            <motion.div initial={{scale: 0, rotate: "-360deg"}} animate={{scale: 1, rotate: "360deg", transition:{duration:.7}}} className="all">
                <div className="list">
                    { items.map(item => 
                        <Item 
                            key={item.id} 
                            index={items.findIndex(x => x.id === item.id)} 
                            id={item.id} 
                            up={this.handleUp} 
                            down={this.handleDown} 
                            title={item.title} 
                            last={this.state.last}
                            del={this.handleDelete}
                            edit={this.handleEdit}
                        />
                    ) }
                </div>
                <div className="add">
                    <input id="new" type="text" />
                    <button onClick={()=>this.handleAdd(document.getElementById("new").value)}>add</button>
                </div>
                <div className="edit">
                    <input id="Edit" type="text" />
                    <button onChange={()=>this.setState({selectedItem: document.getElementById("Edit").value})} disabled={!this.state.selectedItem.code} onClick={()=>this.editeState(this.state.selectedItem.title,this.state.selectedItem.index)}>edit</button>
                </div>
            </motion.div>
        )
    }
}

export default List
