import React, {useEffect, useState} from 'react';
import { motion } from 'framer-motion'

function Item({last, down, up, id, title, index, del, edit}) {
    const [color, setColor] = useState("rgba(0, 0, 0, 0)")
    // const [one, setOne] = useState(true)
    
    useEffect((one) => {
        if(one){ }
        else{
            setTimeout(() => {
                setColor("rgb(212, 66, 9)")
            }, 0);
            setTimeout(() => {
                setColor("rgba(0, 0, 0, 0)")
            }, 250);
        }
    },[index])
    
    function shake(){
        console.log("shake")
    }
    function click(){
        console.log(index, id)
    }
    
    return (
        <motion.div initial={{ transform: "translate(-50%,50%)" }} animate={{ top: `${index * 60}px`, backgroundColor: color }} className="item">
            <div className="left side">
                <motion.button whileHover={{rotate:"360deg"}} className="button" disabled={ !index } onClick={ index === 0 ? ()=>shake() : ()=>up(index)}>
                <svg xmlns="http://www.w3.org/2000/svg" width="26.621" height="17.523" viewBox="0 0 26.621 17.523">
                    <g id="Group_4" data-name="Group 4" transform="translate(-1109.706 -179.582)">
                        <path id="Path_1" data-name="Path 1" d="M1109.849,193.909l12.581-14.222,11.76,14.222" transform="translate(0.98 2.202)" fill="none" stroke="#15d635" strokeWidth="3"/>
                    </g>
                </svg>

                </motion.button>
                <motion.button whileHover={{rotate:"360deg"}} className="button"  disabled={ index + 1 === last } onClick={ ()=>down(index) }>
                <svg xmlns="http://www.w3.org/2000/svg" width="26.621" height="17.523" viewBox="0 0 26.621 17.523">
                    <g id="Group_3" data-name="Group 3" transform="translate(-1109.673 -232.895)">
                        <path id="Path_2" data-name="Path 2" d="M1109.849,193.909l12.581-14.222,11.76,14.222" transform="translate(2245.02 427.798) rotate(180)" fill="none" stroke="#d62915" strokeWidth="3"/>
                    </g>
                </svg>

                </motion.button>
            </div>
            <div onClick={click} className="center">
                {title}
            </div>
            <div className="right side">
                <motion.button whileHover={{rotate:"360deg"}} className="button" onClick={()=> edit(title, index)}>
                <svg xmlns="http://www.w3.org/2000/svg" width="22.943" height="21.668" viewBox="0 0 22.943 21.668">
                    <g id="Group_4" data-name="Group 4" transform="translate(-1113.591 -178.014)">
                        <g id="Rectangle_5" data-name="Rectangle 5" transform="translate(1133.32 178.014) rotate(50)" fill="#79e166" stroke="#707070" strokeWidth="1">
                        <rect width="5" height="22" stroke="none"/>
                        <rect x="0.5" y="0.5" width="4" height="21" fill="none"/>
                        </g>
                        <g id="Polygon_1" data-name="Polygon 1" transform="translate(1120.409 194.081) rotate(111)" fill="#79e166">
                        <path d="M 5.116899967193604 4.5 L 0.8830999732017517 4.5 L 3 0.971833348274231 L 5.116899967193604 4.5 Z" stroke="none"/>
                        <path d="M 3 1.943650007247925 L 1.766190052032471 4 L 4.233809947967529 4 L 3 1.943650007247925 M 3 0 L 6 5 L 0 5 L 3 0 Z" stroke="none" fill="#707070"/>
                        </g>
                    </g>
                </svg>

                </motion.button>
                <motion.button whileHover={{rotate:"360deg"}} className="button" onClick={() => del(index)}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="27.588" height="30.431" viewBox="0 0 27.588 30.431">
                        <g id="Group_3" data-name="Group 3" transform="translate(-1109.206 -226.895)">
                            <g id="Group_6" data-name="Group 6" transform="translate(0.5 -6)">
                            <path id="Path_2" data-name="Path 2" d="M1109.849,193.909l12.581-14.222,11.76,14.222" transform="translate(2245.02 427.798) rotate(180)" fill="none" stroke="#d62915" strokeWidth="3"/>
                            <path id="Path_3" data-name="Path 3" d="M1109.849,193.909l12.581-14.222,11.76,14.222" transform="translate(-0.02 68.424)" fill="none" stroke="#d62915" strokeWidth="3"/>
                            </g>
                        </g>
                    </svg>
                </motion.button>
            </div>
        </motion.div>
    )
}

export default Item
